import $http from "@/common/api/request.js"
import Card from '@/components/common/Card.vue'
import CommodityList from '@/components/common/CommodityList.vue'
import UniNumberBox from '@/components/uni/uni-number-box/uni-number-box.vue'

import {mapMutations} from 'vuex'
	export default {
		data() {
			return {
				isShow:false,
				animationData:{},
				goodsContent:{},
				num:1,
				swiperList:[
					{imgUrl:"../../static/img/details1.jpeg"},
					{imgUrl:"../../static/img/details2.jpeg"},
					{imgUrl:"../../static/img/details3.jpeg"}
				],
				dataList:[
					{
						id:1,
						imgUrl:"../../static/img/commodity1.jpg",
						name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
						pprice:"299",
						oprice:"659",
						discount:"5.2"
					},
					{
						id:2,
						imgUrl:"../../static/img/commodity2.jpg",
						name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
						pprice:"299",
						oprice:"659",
						discount:"5.2"
					},
					{
						id:3,
						imgUrl:"../../static/img/commodity3.jpg",
						name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
						pprice:"299",
						oprice:"659",
						discount:"5.2"
					},
					{
						id:4,
						imgUrl:"../../static/img/commodity4.jpg",
						name:"大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008大姨绒毛大款2020年必须买,不买你就不行了,爆款疯狂GG008",
						pprice:"299",
						oprice:"659",
						discount:"5.2"
					}
				]
			}
		},
		components:{
			Card,
			CommodityList,
			UniNumberBox
		},
		onLoad(e){
			this.getData(e.id)
		},
		//修改返回的默认行为
		onBackPress(){
			if(this.isShow === true){
				this.hidePop()
				return true
			}
		},
		//点击分享
		onNavigationBarButtonTap(e){
			if(e.type ==='share'){
				uni.share({
					"provider":"weixin",
					"type":0,
					"scene":"WXSceneSession",
					"title":this.goodsContent.name,
					"href":"http://192.168.0.108:8080/#/pages/details/details?id="+this.goodsContent.id+"",
					// imageUrl:this.goodsContent.imgUrl,
					success: function (res) {
					        uni.showTabBar({
					        	title:"分享成功"
					        })
					},
					fail: function (err) {
						console.log("fail:" + JSON.stringify(err));
					}
				})
			}
		},
		methods: {
			...mapMutations(['addShopCart']),
			//改变商品数量
			changeNumber(value){
				this.num = value
			},
			//请求商品
			getData(id){
				$http.request({
					url:"/goods/id",
					data:{
						id:id
					}
				}).then((res)=>{
					this.goodsContent = res[0]
				}).catch(()=>{
					uni.showToast({
						title:'请求失败',
						icon:'none'
					})
				})
			},
			showPop(){
				var animation = uni.createAnimation({
				      duration: 200
				})
				animation.translateY(600).step()
				this.animationData = animation.export()
				this.isShow = true
				setTimeout(()=>{
					animation.translateY(0).step()
					this.animationData = animation.export()
				},200)
			},
			hidePop(){
				var animation = uni.createAnimation({
				      duration: 200
				})
				animation.translateY(600).step()
				this.animationData = animation.export()
				
				
				setTimeout(()=>{
					animation.translateY(0).step()
					this.isShow = false
				},200)
			},
			//跳转到购物车页面
			goShopCart(){
				uni.switchTab({
					url:'../shopcart/shopcart'
				})
			},
			//加入购物车
			addCart(){
				let goods = this.goodsContent
				this.goodsContent['checked'] = false
				this.goodsContent['num'] = this.num
				//加入购物车
				this.addShopCart(goods)
				//隐藏底部弹出框
				this.hidePop()
				//提示信息
				uni.showToast({
					title:'成功加入购物车',
					icon:'none'
				})
			}
		}
	}
